$yellow: #FFCE00;
$gray: rgba(0, 0, 0, 0.11);

$transition: all 500ms cubic-bezier(0.420, 0.000, 0.580, 1.000);

.container {
  min-width: 1200px;
  width: calc(100% - 320px);
  margin: 0 auto;
}

.icon {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  $yellow: #FFCE00;
  transition: $transition;
}

.section-heading {
  color: $gray;
  text-transform: uppercase;
  font-size: 3.5em;
  font-family: "MontserratBlack";
}

.section-subheading {
  text-transform: uppercase;
  font-family: "MontserratBlack";
  font-size: 1.5em;
  position: relative;
  bottom: 20px;
  span {
    font-size: inherit;
    color: $yellow;
  }
}

.list-dropdown {
  transition: $transition;
  max-height: 1em;
  position: relative;
  display: flex;
  align-items: flex-start;
  flex-direction: column-reverse;
  justify-content: flex-end;
  li {
    opacity: 0;
    transition: $transition;
    padding-bottom: 5px;
    &:last-of-type {
      opacity: 1;
    }
  }
  &:hover {
      max-height: 1em;
      overflow: visible;
      transition: $transition;
      &>li {
        opacity: 1;
        transition: $transition;
        &>a:hover {
          color: $yellow;
        }
      }
      &::after {
        transition: $transition;
        border-width: 4px 0 4px 8px;
        border-color: transparent transparent transparent $yellow;
      }
  }
  &::after {
    content: '';
    display: block;
    border-style: solid;
    border-width: 7px 5px 0 5px;
    border-color: #ffffff transparent transparent transparent;
    position: absolute;
    right: 0px;
    bottom: 0;
    transition: $transition;
  }
}

.filter-list {
  display: flex;
  padding: 20px 0;
  .filter-item {
    margin: 10px 0;
    margin-right: 20px;
    color: #848484;
    position: relative;
    text-transform: uppercase;
    cursor: pointer;
    font-size: 14px;
    &::after {
      content: '';
      display: block;
      height: 1px;
      width: 0;
      background-color: $yellow;
      position: absolute;
      bottom: 0;
      left: 0;
      transition: $transition;
    }
    &:hover::after {
      width: 100%;
      transition: $transition;
    }
    &.active {
      color: black;
      &::after {
        width: 100%;
      }
    }
  }
}

h6 {
  text-transform: uppercase;
  font-family: "MontserratBlack";
  font-size: 1em;
}

h3 {
  font-family: MontserratBlack;
  font-size: 2.5em;
  padding-bottom: 5px;
  position: relative;
  text-transform: uppercase;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


@mixin range-slider($width, $height, $input-top, $input-bg-color, $input-thumb-color, $float:none, $input-height:5px, $input-border-radius:14px) {
  position: relative;
  width: $width;
  height: $height;

  input[type="range"] {
    // pointer-events: none;
    position: absolute;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    border: none;
    border-radius: $input-border-radius;
    background: $input-bg-color;
    // box-shadow: inset 0 1px 0 0 darken($input-bg-color,15%), inset 0 -1px 0 0 darken($input-bg-color,10%);
    // -webkit-box-shadow: inset 0 1px 0 0 darken($input-bg-color,15%), inset 0 -1px 0 0 darken($input-bg-color,10%);
    left: 0;
    top: $input-top;
    width: $width;
    outline: none;
    height: $input-height;
  }

  input[type="range"]::-webkit-slider-thumb {
    pointer-events: all;
    position: relative;
    z-index: 1;
    outline: 0;
    -webkit-appearance: none;
    width: $input-height;
    height: $input-height;
    border: none;
    border-radius: $input-border-radius;

    height: 15px;
    width: 15px;
    border-radius: 15px;
    background: $yellow;
    cursor: pointer;
  }

  input[type="range"]::-moz-range-thumb {
    pointer-events: all;
    position: relative;
    z-index: 10;
    -moz-appearance: none;
    width: $input-height;
    height: $input-height;
    border: none;
    border-radius: $input-border-radius;
    background-image: linear-gradient(to bottom, lighten($input-thumb-color,60%) 0, $input-thumb-color 100%); /* W3C */
  }

  input[type="range"]::-ms-thumb {
    pointer-events: all;
    position: relative;
    z-index: 10;
    -ms-appearance: none;
    width: $input-height;
    height: $input-height;
    border-radius: $input-border-radius;
    border: 0;
    background-image: linear-gradient(to bottom, lighten($input-thumb-color,60%) 0, $input-thumb-color 100%); /* W3C */
  }

  input[type=range]::-moz-range-track {
    position: relative;
    z-index: -1;
    background-color: rgba(0, 0, 0, 1);
    border: 0;
  }

  input[type=range]:last-of-type::-moz-range-track {
    -moz-appearance: none;
    background: none transparent;
    border: 0;
  }

  input[type=range]::-moz-focus-outer {
    border: 0;
  }
}


div.selection-filter {
  @include range-slider(100%, 300px, 50%, #23221c, #413F41, left);
}

/* MOBILE BUTTON*/
button.dots {
  width:60px;
  height:30px;
  border:none;
  background:transparent;
  position:relative;
  cursor:pointer;
  margin:50px;
  &:focus {
    outline:none;
  }

  &.dots {
      &:after,
      &:before,
      span {
        width:10%;
        height:20%;
        border-radius:100px;
        position:absolute;
        left:0px;
        background: $yellow;
        transform:rotate(0deg);
        transition: all 0.4s;

        left: 1%;
        right: 1%;
        margin: 0 auto;

      }

      &:after,
      &:before {
        content:"";
      }

      &:after {
        top:-10%;
        margin-top:0px;
        
      }

      &:before {
        bottom:-10%;
        margin-bottom:0px;
      }

      span {
        top:50%;
        margin-top:-2px;
      }

      &.on {

        &:after {
          transform:rotate(135deg)translate(9px,-9px);
          width: 100%;
        }

        &:before { 
          transform:rotate(225deg);
          bottom:50%;
          margin-bottom:-2px;
          width: 100%;
        }

        span {
          transform:rotate(135deg);
        }

      }
  }
}

.mobile-link-logo {
  display: none;
  width: 100px;
  margin: 30px;
  float: right;
}
