/* ----------- Non-Retina Screens(Laptops) ----------- */
@media screen
  and (min-device-width: 1200px)
  and (max-device-width: 1600px)
  and (-webkit-min-device-pixel-ratio: 1) {
    .triangle.top-left, .triangle.bottom-left  {
      width: 85%;
    }
    .triangle.bottom-left {
      height: 200px;
    }
    .triangle.top-right {
      width: 15%;
      height: 50px;
      // height: 5%;
    }
    .triangle.bottom-right {
      width: 15%;
      height: 10%;
    }
    .about.about-company .triangle.top-right {
      width: 15%;
      height: 10%;
    }


    .header-page.large {
      height: 90vh;
      .triangle {
        bottom: 50px;
      }
    }
    
    .navigation .menu .menu-item {
      padding: 20px 10px;
      &.logo img {
        width: 100px;
      }
    }
    .slogan {
      padding-top: 50px;
    }

    .about .left-wrapper .left-content p {
      width: 100%;
    }

    .about.about-company {
      transform: translateY(-50px);
      .about-left {
        grid-column-start: 1;
        grid-column-end: 10;
        &::after {
          border-width: 0 0 200px 350px;
        }
      }
      .about-right {
        grid-column-start: 10;
      }
    }
    .partners .partners-list {
      .list-item {
        width: 170px;
      }
    }
    .footer-wrapper .footer-info {
      padding-top: 20px;
      a {
        font-size: 12px;
      }
      .info-list {
        grid-template-columns: repeat(11,1fr);
        .info-list-item {
          grid-column: span 3;
          p {
            font-size: 12px;
          }
          &:last-of-type {
            grid-column: span 2;
          }
        }
      }
    }

    .about.about-project {
      transform: translateY(-50px);
    }

    .about.about-company-page {
      .about-left {
        grid-column-end: 11;
      }
    }

    .last-news.news .news-wrapper .list-item:nth-child(4n) svg {
      height: 300px;
    }
}

@media only screen and (max-width: 1200px) {
  .container {
    min-width: auto;
    width: calc(100% - 40px);
  }

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .dots {
      display: inline-block;
    }
    .mobile-link-logo {
      display: inline-block;
      position: relative;
      z-index: 10;
    }
    &.fixed {
      top: 0;
    }
  }

  .navigation.mobile-nav {
    height: 100%;
    border: 1px solid rebeccapurple;
    position: fixed;
    top: 0;
    left: -100vw;
    width: 100vw;
    min-width: 100vw;
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // transition: $transition;
    .menu {
      display: flex;
      flex-direction: column;
      .menu-item.logo {
       display: none; 
      }
    }
    &.active {
      display: flex;
    }
  }

  .navigation.mobile-nav.active {
    display: flex;
    left: 0;
    
  }
}

